.navigation {
    width: 100%;
    margin-top: 30px;
    padding: 0 40px;

    ul {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;

        li {
            margin: 0 60px;

            img {
                width: 40px;
                height: 40px;

                @media screen and (max-width: $mobile-breakpoint) {
                  width: 40px;
                  height: 40px;
              }
            }

            @media screen and (max-width: $mobile-breakpoint) {
              margin: 0 28px;
          }
        }

        .nav-active {
            position: relative;
      
            &::after {
              content: "";
              height: 4px;
              width: 0;
              background: $color-1;
              position: absolute;
              border-radius: 10px;
              bottom: 0px;
              left: 50%;
              transform: translateX(-50%);
              -webkit-animation: anim 0.3s ease forwards;
              animation: anim 0.6s ease forwards;
      
              @-webkit-keyframes anim {
                to {
                  width: 100%;
                }
              }
              @keyframes anim {
                to {
                  width: 100%;
                }
              }
            }
          }
    }

  @media screen and (max-width: $mobile-breakpoint) {
      width: 60%;
  }
}